.form {
    background-color: rgb(255, 255, 255);
    text-align: center;
    border: 10px solid rgb(0, 0, 0);
    padding: 4px;
    width: 220px;
}
.iframe {
    border:0;
    width: 250px;
    height: 370px;
}
.ul {
    text-align: right;
    list-style-type:circle;
}
.flex {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
@media only screen and (min-width: 768px) {
    .flex {
        flex-direction: row;
    }
    .iframe {
        width: 400px;
    }
    .form {
        width: fit-content;
    }
}