@keyframes open {
  from {
    height: 0px;
    width: 100vw;
  }
  to {
    height: 251px;
    width: 100vw;
  }
}

.header1 {
  background-color: rgb(172, 129, 10);
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.anim {
  animation-name: open;
  animation-duration: 2s;
  
}
.header2 {
  background-color: rgb(172, 129, 10);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 28px;
  flex-direction: row;
  display: none;
}
.headerdivs {
  padding: 12px 4px;
  width: 98vw;
}
.headerdivs:hover {
  background: rgb(219, 179, 67);
}
.a {
  text-decoration: none;
  color: black;
}
.a:active {
  color: rgb(100, 100, 100);
}
.active {
  background: rgb(219, 197, 138);
}
.amanager {
  text-decoration: none;
  color: black;
  font-size: 0.6rem;
}
.amanager:active {
  color: rgb(100, 100, 100);
}
.aheader1 {
  text-decoration: none;
  color: black;
  display: none;
}
.aheader1:active {
  color: rgb(100, 100, 100);
}
.menubtn {
  background: rgb(172, 129, 10);
  border: 2px solid black;
  margin: 5px;
  transform: rotate(90deg);
  padding: 5px;
  padding-bottom: 7px;
}
.menubtn:hover {
  cursor: pointer;
  background:rgb(219, 179, 67);
}
.menubtn:active {
  background:rgb(231, 197, 102);
}
.backbtn {
  background: black;
  width: 100vw;
}
@media only screen and (min-width: 768px) {
  .header1 {
   display: none;
  }
  .header2 {
    display: flex;
  }
  .headerdivs {
    padding: 12px 26px;
    width: auto;
  }
  .amanager {
    font-size: 1rem;
  }
}

