
.table {
    margin-left: auto;
    margin-right: auto;
    text-align: right;
}
.ol {
    text-align: right;
    font-size: 14px;
}
.ul {
    text-align: right;
    font-size:14px;
}
.a:hover {
    text-decoration: none;
    color: rgb(100, 100, 100);
}
.a {
    text-decoration: none;
    color: black;
}

.a:active {
    color: rgb(141, 192, 83);
}
@media only screen and (min-width: 768px) {
    .ol {
        font-size: 20px;
    }
    .ul {
        font-size: 20px;
    }
}