.flex {
    margin: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow-x: auto;
    
}
.tablePhone {
    border: solid black 1px;
}
.tableComp {
    border: solid black 1px;
    display: none;
}
.th {
    border: solid black 1px;
    padding: 5px 10px;
}
.tr {

}
.thead {
    background: rgb(209, 129, 71);
}
@media only screen and (min-width: 700px) {
    .tablePhone {
        display: none;
    }
    .tableComp {
        display: inline;
    }
}