.arrow {
    width:15px;
    height:15px;
    border: 2px solid rgb(0, 0, 0);
    padding: 1px;
    border-radius: 50px;
}
.img {
    width: 180px;
}
img.arrow {
    cursor: pointer;
    margin-top: 60px;
}
img.arrow:hover {
    opacity: 0.6;
}
img.arrow:active {
    opacity: 0.4;
}
.photos {
    display: flex;
    justify-content: center;
}


@media only screen and (min-width: 768px) {
    .arrow {
        width:30px;
        height:30px;
        border: 6px solid rgb(0, 0, 0);
        padding: 4px;
        border-radius: 50px;
    }
    .img {
        width: 400px;
    }
    
    img.arrow {
        margin-top: 150px;
    }
    .flex {
        display: flex;
        justify-content: space-around;
    }
}